main {
  padding: 0 20px 20px;
  min-height: calc(
    100vh - 140px
  ); /* Adjust height to ensure footer is at the bottom */
  margin-top: 10px; /* Adjusted to reduce space below breadcrumbs */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
}

/* Stylizacja header */
header {
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 100" fill="%23333333"><path d="M0 0v100c250 0 375-24 500-48 125 24 250 48 500 48V0H0Z" opacity=".5"></path><path d="M0 0v4c250 0 375 24 500 48C625 28 750 4 1000 4V0H0Z"></path></svg>');
  background-size: cover;
  background-position: top;
  color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-between; /* Zmiana na space-between */
  align-items: center; /* Wyrównanie elementów pionowo */
  padding: 10px 20px;
  position: relative;
  z-index: 1000; /* Ensure header is above other elements */
  transition: padding 0.3s ease-in-out;
}

.header-left {
  display: flex;
  align-items: center;
  width: auto;
  justify-content: flex-start;
}

.header-left .logo {
  width: 150px;
  height: auto;
  margin-right: 10px;
  transition: width 0.3s ease-in-out;
}

.header-left h1 {
  color: #ac2a21; /* Blood red color */
  font-family: "Raleway", "Trebuchet MS", sans-serif; /* Font stack */
  margin: 0; /* Dodano margin 0 dla wyrównania */
}

/* Stylizacja menu */
nav.menu {
  display: flex;
  justify-content: flex-end; /* Wyrównanie menu do prawej */
  align-items: center;
  text-align: center;
  color: #fff;
  position: relative;
  padding: 5px;
  width: auto;
  z-index: 999; /* Z-index poniżej header.sticky */
  margin: 0;
  /* From https://css.glass */
  background: rgba(51, 51, 51, 0.2);
  border-radius: 20px;
  box-shadow:
    2px 2px 15px rgba(255, 255, 255, 0.4),
    4px 4px 30px rgba(255, 255, 255, 0.2),
    8px 8px 45px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 0.5px solid rgba(255, 255, 255, 0.1);
}

nav.menu ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: fit-content;
  z-index: 1; /* Z-index dla ul */
}

nav.menu ul li {
  margin: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 105px;
  height: 70px;
  overflow: visible; /* Ustawienie overflow na visible */
  position: relative; /* Ustawienie pozycji względnej dla "pływającej" ikony */
}

nav.menu ul li a {
  color: #fff;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  transition: all 0.3s ease;
  position: relative;
  z-index: 1; /* Z-index dla linku, aby tekst był pod ikoną */
}

nav.menu ul li.active a {
  color: #ac2a21; /* Highlight color for the active page */
  text-shadow:
    0px 0px 4px rgba(255, 255, 255, 0.4),
    -1px -1px 4px rgba(255, 255, 255, 0.4),
    1px -1px 4px rgba(255, 255, 255, 0.4),
    -1px 1px 4px rgba(255, 255, 255, 0.4),
    2px 2px 8px rgba(255, 255, 255, 0.2),
    -2px -2px 8cap rgba(255, 255, 255, 0.2),
    2px -2px 8px rgba(255, 255, 255, 0.2),
    -2px 2px 8px rgba(255, 255, 255, 0.2),
    4px 4px 16px rgba(255, 255, 255, 0.1),
    -4px -4px 16px rgba(255, 255, 255, 0.1),
    -4px 4px 16px rgba(255, 255, 255, 0.1),
    4px -4px 16px rgba(255, 255, 255, 0.1);
  font-weight: bold;
}

nav.menu ul li a:focus,
nav.menu ul li a:hover {
  color: #fff; /* White text color */
  outline: none; /* Remove default outline */
  position: relative;
  text-shadow: none;
  border-radius: 12px;
}

nav.menu ul li a ion-icon {
  font-size: 24px;
  transition: all 0.3s ease;
  background-color: transparent;
  border-radius: 10px;
  border: 2px solid #fff; /* Border wokół ikony */
  padding: 10px;
  position: absolute; /* Pozycjonowanie absolutne dla "pływającej" ikony */
  z-index: 1001; /* Wyższy z-index niż header.sticky, aby ikona była na wierzchu */
  top: 50%; /* Umieszczenie ikony na środku */
  transform: translateY(-50%); /* Wyśrodkowanie ikony */
}

nav.menu ul li a span {
  opacity: 0; /* Ukryty tekst */
  transition: all 0.3s ease;
  position: absolute;
  white-space: nowrap; /* Zapobiega łamaniu tekstu */
  z-index: 1; /* Upewnienie się, że tekst znajduje się poniżej ikony */
}

nav.menu ul li a:focus span,
nav.menu ul li a:hover span {
  background-color: rgba(172, 42, 33, 0.6);
  -webkit-box-shadow: 0px 0px 5px 5px rgba(172, 42, 33, 0.6);
  -moz-box-shadow: 0px 0px 5px 5px rgba(172, 42, 33, 0.6);
  box-shadow: 0px 0px 5px 5px rgba(172, 42, 33, 0.6);
  border-radius: 10px;
  padding: 5px;
  border: none; /* Usunięcie borderu */
}

nav.menu ul li a:hover ion-icon,
nav.menu ul li.active a ion-icon {
  transform: translateY(-80%); /* Przesunięcie ikony w górę */
  background-color: rgba(51, 51, 51, 0.8); /* Okrągłe tło */
  border-radius: 50%;
  border: none; /* Usunięcie borderu */
}

nav.menu ul li a:hover ion-icon {
  -webkit-box-shadow: 0px 0px 10px 5px rgba(172, 42, 33, 0.6);
  -moz-box-shadow: 0px 0px 10px 5px rgba(172, 42, 33, 0.6);
  box-shadow: 0px 0px 10px 5px rgba(172, 42, 33, 0.6);
  background-color: rgba(172, 42, 33, 0.6);
}

nav.menu ul li a:hover span,
nav.menu ul li.active a span {
  opacity: 1; /* Odsłonięcie tekstu */
  transform: translateY(85%); /* Przesunięcie tekstu w dół */
}

/* Stylizacja ikony menu (hamburger) */
.menu-toggle {
  position: relative;
  box-shadow: none;
  display: none;
  align-items: center;
  justify-content: flex-end;
}

.menu-toggle,
.menu-toggle i {
  font-size: 24px;
  cursor: pointer;
  color: #ac2a21;
  background: none;
  border: none;
}

.menu-toggle:hover {
  background: none;
  border: none;
  box-shadow: none;
}

/* Stylizacja breadcrumbs */
.breadcrumb {
  background-color: transparent;
  padding: 5px 0 0 5px; /* Adjusted to reduce space */
  margin: 5px 0 0 5px; /* Adjusted to reduce space */
  list-style: none;
  display: flex;
  align-items: center;
  font-size: 0.85em;
}

.breadcrumb a {
  color: #ac2a21;
  text-decoration: none;
}

.breadcrumb a:hover {
  text-decoration: underline;
}

/* Style dla sticky menu i nagłówka */
header.sticky {
  position: fixed;
  width: 100%;
  z-index: 1000;
  top: 0;
}

header.sticky .header-left,
nav.menu.sticky ul {
  align-items: center;
}

/* Footer Style */
footer {
  color: #fff;
  text-align: center;
  padding: 100px 0 0;
  position: relative; /* Make footer relative to content */
  bottom: 0;
  width: 100%;
}

.custom-shape-divider-top-1725739292 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  rotate: 180deg;
}

.custom-shape-divider-top-1725739292 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 100px;
}

.custom-shape-divider-top-1725739292 .shape-fill {
  fill: #333;
}

.footer-container {
  background-color: #333;
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    minmax(250px, 1fr)
  ); /* Kolumny dostosowują się do szerokości ekranu */
  gap: 20px; /* Odstępy między kolumnami */
  justify-content: space-between;
  margin: 0 auto;
  padding: 0 10px;
  font-size: smaller;
  position: relative;
  padding-bottom: 50px;
}

.footer-section {
  flex: 1;
  padding: 0 20px;
}

.contact-sec i {
  margin: 5px;
}
.footer-section h4 {
  margin-bottom: 10px;
  font-variant: small-caps;
}

footer div.privacy ul {
  list-style-type: none;
  padding: 0;
}

footer div.privacy ul li {
  margin-bottom: 5px;
}

footer .privacy a {
  color: #fff;
  text-decoration: none;
}

footer div.social a {
  color: #f0f0f0;
  padding: 0.5em;
}

footer div.social a:hover {
  color: #ac2a21;
}

.custom-shape-divider-bottom-1726092730 {
  position: absolute;
  bottom: 0; /* Przyklei divider do góry elementu */
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  rotate: 180deg;
}

.custom-shape-divider-bottom-1726092730 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 50px;
}

.custom-shape-divider-bottom-1726092730 .shape-fill {
  fill: #111111; /* Kolor kształtu */
}

footer div.copyright {
  background-color: #111;
  /*-webkit-box-shadow: inset 0px 0px 24px 4px rgba(51, 51, 51, 1);
    -moz-box-shadow: inset 0px 0px 24px 4px rgba(51, 51, 51, 1);
    box-shadow: inset 0px 0px 24px 4px rgba(51, 51, 51, 1);*/
}

.privacy a:hover {
  color: #ac2a21;
  text-decoration: underline;
}

/* Mobile responsive */
@media only screen and (max-width: 768px) {
  header {
    padding: 5px 10px;
    width: 100%;
  }

  .header-left {
    /* From https://css.glass */
    background: rgba(255, 255, 255, 0.2);
    border-radius: 10px;
    box-shadow:
      2px 2px 15px rgba(255, 255, 255, 0.4),
      4px 4px 30px rgba(255, 255, 255, 0.2),
      8px 8px 45px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 0.5px solid rgba(255, 255, 255, 0.1);
  }

  header.sticky {
    box-shadow: none;
  }

  .header-left .logo {
    width: 120px;
  }

  header.sticky .header-left .logo {
    width: 80px;
  }

  .header-left h1 {
    display: none;
  }

  .menu-toggle {
    display: block;
  }

  nav.menu {
    display: flex;
    justify-content: flex-end; /* Align menu to the right */
    background: none;
    border-radius: none;
    box-shadow: none;
    backdrop-filter: none;
    -webkit-backdrop-filter: none;
    border: none;
  }

  @keyframes menu-opacityup {
    0% {
      opacity: 0;
    }
    10% {
      opacity: 0.1;
    }
    20% {
      opacity: 0.2;
    }
    30% {
      opacity: 0.3;
    }
    40% {
      opacity: 0.4;
    }
    50% {
      opacity: 0.5;
    }
    60% {
      opacity: 0.6;
    }
    70% {
      opacity: 0.7;
    }
    80% {
      opacity: 0.8;
    }
    90% {
      opacity: 0.9;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes menu-opacitydown {
    0% {
      opacity: 1;
    }
    10% {
      opacity: 0.9;
    }
    20% {
      opacity: 0.8;
    }
    30% {
      opacity: 0.7;
    }
    40% {
      opacity: 0.6;
    }
    50% {
      opacity: 0.5;
    }
    60% {
      opacity: 0.4;
    }
    70% {
      opacity: 0.3;
    }
    80% {
      opacity: 0.2;
    }
    90% {
      opacity: 0.1;
    }
    100% {
      opacity: 0;
    }
  }

  nav.menu ul {
    display: none;
    flex-direction: column;
    align-items: center;
    position: absolute; /* Position relative to nav.menu */
    top: 50%;
    right: 0;
    background-color: rgba(51, 51, 51, 0.8);
    padding: 10px 20px;
    border-radius: 5px;
    width: max-content;
    transition: max-height 1s ease-out; /* Smooth animation */
    overflow: hidden;
    max-height: 0; /* Start with hidden menu */
    z-index: 1000; /* Ensure the menu is above other content */
    backdrop-filter: blur(10px); /* Efekt rozmycia tła */
    -webkit-backdrop-filter: blur(10px); /* Efekt rozmycia dla Safari */
    box-shadow: 0 0 20px rgba(51, 51, 51, 0.7); /* Rozpływające się krawędzie */
  }

  nav.menu ul.active {
    display: flex;
    max-height: 500px; /* Adjust as needed */
  }

  nav.menu ul li {
    margin: 5px 0;
  }

  nav.menu ul li a {
    width: 100%;
    padding: 10px;
    color: #fff;
    text-align: center;
    text-decoration: none;
  }

  nav.menu ul li a span {
    opacity: 1;
    position: static;
    transform: translateY(90%);
  }

  nav.menu ul li a ion-icon {
    transform: translateY(-90%);
  }

  nav.menu ul li.active a ion-icon {
    transform: translateY(-90%);
  }

  nav.menu ul li.active a span {
    transform: translateY(90%);
  }

  .footer-container {
    display: grid;
    justify-content: center;
  }

  .footer-section {
    margin: 10px auto;
  }

  .copyright {
    font-size: smaller;
  }
}

/* Stylizacja scrollToTopBtn */
#scrollToTopBtn {
  display: block; /* Ensure the button is part of the layout */
  opacity: 0; /* Start with the button hidden */
  position: fixed; /* Fixed/sticky position */
  bottom: 20px; /* Place the button at the bottom of the page */
  right: 30px; /* Place the button 30px from the right */
  z-index: 99; /* Make sure it does not overlap */
  border: none; /* Remove borders */
  outline: none; /* Remove outline */
  background-color: #ac2a217d; /* Set a background color */
  color: white; /* Text color */
  cursor: pointer; /* Add a mouse pointer on hover */
  padding: 10px; /* Some padding */
  border-radius: 100%; /* Rounded corners */
  font-size: 12px; /* Increase font size */
  transition:
    opacity 0.3s ease-in-out,
    transform 0.3s ease-in-out; /* Smooth zoom transition */
}

#scrollToTopBtn.show {
  opacity: 1; /* Make the button visible */
}

#scrollToTopBtn #long {
  opacity: 0;
  transition: opacity 0.15s ease-in-out;
}

#scrollToTopBtn:hover {
  background-color: #555; /* Add a dark-grey background on hover */
  transform: scale(1.2); /* Zoom in on hover */
}

#scrollToTopBtn:hover #short {
  opacity: 0;
  transition: opacity 0.15s ease-in-out;
}

#scrollToTopBtn:hover #long {
  opacity: 1;
  transition: opacity 0.15s ease-in-out;
}

#scrollToTopBtn svg {
  width: 24px;
  height: 24px;
}

button {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition:
    transform 0.3s ease,
    box-shadow 0.3s ease-in-out;
  -moz-transition:
    transform 0.3s ease,
    box-shadow 0.3s ease-in-out;
  -ms-transition:
    transform 0.3s ease,
    box-shadow 0.3s ease-in-out;
  -o-transition:
    transform 0.3s ease,
    box-shadow 0.3s ease-in-out;
}

button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 10px rgba(0, 0, 0, 0.2);
  background-color: #555; /*Darken background color on hover*/
}

button:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    120deg,
    transparent,
    rgba(146, 148, 248, 0.4),
    transparent
  );
  transition: all 650ms;
}

button:hover:before {
  left: 100%;
}

.fade-in {
  opacity: 0;
  transform: translateY(20px);
  transition:
    opacity 0.6s ease-out,
    transform 0.6s ease-out;
}

.fade-in.visible {
  opacity: 1;
  transform: translate(0);
}

/* Add2Any Style */

/* Button styling */
#toggle-share-buttons {
  position: fixed;
  top: 50%;
  left: 0;
  padding: 10px;
  background-color: #ac2a21;
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 0 5px 5px 0;
  margin-right: 10px;
  z-index: 1000;
}

#toggle-share-buttons:hover {
  background-color: #555;
}

/* Sharing panel styling */
.a2a_sticky {
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: #ffffff;
  border-radius: 5px;
  padding: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  display: none; /* Domyślnie ukryty */
  z-index: 1001;
}

.a2a_sticky a {
  display: block;
  margin-bottom: 5px;
}

.a2a_sticky a:last-child {
  margin-bottom: 0;
}
